import {vanButton} from '@/wxcomponents/vant-weapp/button/index.js' 

export default {
	name: 'CtUploader',
	components: {
		vanButton
	},
	props: {
		fileList: {
			type: Array,
			default: () => []
		},
		afterRead: {
			type: Function
		},
		maxCount: {
			type: Number,
			default: 5
		},
		beforeDelete: {
			type: Function
		}
	},
	data() {
		return {
			
		};
	},
	methods: {
		/**
		 * 删除事件回调
		 * @param {Object} url
		 */
		handleDelete(url) {
			this.beforeDelete({
				url
			})
		},
		/**
		 * 上传照片
		 */
		uploadImg() {
			const $vm = this
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
							$vm.afterRead(res.tempFilePaths)
			    }
			});
		}
	},
}
